import React  from "react";
import { Input, Alert, Descriptions, Form, Button, Row, Col, message, Image, Space, PageHeader, Card} from 'antd'
import { useApiSelector } from 'src/hooks'
import { useParams, useHistory } from '@friday/router'
import { dispatchAsync, useRequest } from '@friday/async'
import { isEmpty, get, isNumber, pick, omit } from 'lodash'
import { useConfiguration } from '@friday/core'
import ModalVideo from './ModalVideo'
import './style.less'

const FormItem = Form.Item

const Index = () => {

    const apis = useApiSelector()

    const [ form ] = Form.useForm()

    const { id } = useParams() as any

    const { publicUrl } = useConfiguration()

    const { dataJson } = useRequest(apis.admin.reviewDetail({id}))

    const history = useHistory()

    React.useEffect(() => {
        if (!isEmpty(dataJson)) {
            form.setFieldsValue(dataJson)
        }
    }, [dataJson])


    const onFinish = async (type) => {
        const values = await form.validateFields()

        const respone = await dispatchAsync(apis.admin.saveOrUpdate({
            ...values,
            signupId: id,
            save: type
        })) as any
        
        if (respone.error) return 
        message.success('')
        history.goBack()
    }

    const imgList = eval(get(dataJson, 'productPics')) || []

    const videoList = eval(get(dataJson, 'productVideos')) || []
    

    return (
        <div>
            <PageHeader 
                title=''
                ghost={false}
                onBack={() => history.goBack()}
            />
            <Card hoverable bodyStyle={{padding: 0}}>
            <Alert message= '' className="m-fix-alert" banner  showIcon={false} />
            <Descriptions bordered  className='mg-t-20 mg-b-20' layout="vertical" size={'small'}>
                <Descriptions.Item label="">{get(dataJson, 'id')}</Descriptions.Item>
                <Descriptions.Item label="">{get(dataJson, 'entryDirection')}</Descriptions.Item>
                <Descriptions.Item label="">{get(dataJson, 'productType')}</Descriptions.Item>
                <Descriptions.Item label="()">{get(dataJson, 'productName')}</Descriptions.Item>
                <Descriptions.Item label="()" >
                    {get(dataJson, 'productNameEn') || '--'}
                </Descriptions.Item>
                <Descriptions.Item label="">
                    {get(dataJson, 'recommendName') || '--'}
                </Descriptions.Item>
                <Descriptions.Item label="" span={3}>
                    <Image.PreviewGroup>
                        {imgList.map((item, index) => {
                            return (
                                <Image
                                    width={200}
                                    key={index}
                                    src={publicUrl.OPEN_IMG_URL + item}
                                />
                            )
                        })}
                    </Image.PreviewGroup>
                </Descriptions.Item>
                <Descriptions.Item label="" span={3}>
                    <Space>
                        {videoList.map((item, index) => {
                            return (
                                <ModalVideo title={`${index+1}`} url={publicUrl.OPEN_IMG_URL + item} key={index} />
                            )
                        })} 
                    </Space> 
                </Descriptions.Item>
                <Descriptions.Item label="()" span={3}>
                    {/* {get(dataJson, 'productDesc') || '--'} */}
                    <div>
                        <span style={{color: '#666'}}>:</span> {get(dataJson, 'explainA') || '--'}
                    </div>
                    <div>
                        <span style={{color: '#666'}}>:</span> {get(dataJson, 'explainB') || '--'}
                    </div>
                    <div>
                        <span style={{color: '#666'}}>:</span> {get(dataJson, 'explainC') || '--'}
                    </div>
                    <div>
                        <span style={{color: '#666'}}>:</span> {get(dataJson, 'explainD') || '--'}
                    </div>
                    <div>
                        <span style={{color: '#666'}}>:</span> {get(dataJson, 'explainE') || '--'}
                    </div>
                </Descriptions.Item>
                
            </Descriptions>
            
            <div className="m-review-line">

            </div>
            
            <h3></h3>

            <Form
                form={form}
                labelCol={{
                    xs:8,
                    md:6
                }}
                wrapperCol={{
                    xs: 12,
                    md: 12
                }}
                className='mg-t-30'
            >
                <FormItem
                    label={'(20)'}
                    rules={[{required: true, message: '1-20', pattern: /^([01]?\d|20)$/ }]}
                    name='itemA'
                >
                    <Input placeholder='1-20' disabled={dataJson.save == 1}  suffix=''  />
                </FormItem>

                <FormItem
                    label={'(20)'}
                    rules={[{required: true, message: '1-20', pattern: /^([01]?\d|20)$/}]}
                    name='itemB'
                >
                    <Input placeholder='1-20' disabled={dataJson.save == 1} suffix=''   />
                </FormItem>

                <FormItem
                    label={'(20)'}
                    rules={[{required: true, message: '1-20', pattern: /^([01]?\d|20)$/}]}
                    name='itemC'
                >
                    <Input placeholder='1-20' disabled={dataJson.save == 1} suffix=''  />
                </FormItem>

                <FormItem
                    label={'(15)'}
                    rules={[{required: true, message: '1-15', pattern:  /^([01]?[0-5])$/}]}
                    name='itemD'
                >
                    <Input placeholder='1-15' disabled={dataJson.save == 1}  suffix=''  />
                </FormItem>

                <FormItem
                    label={'(15)'}
                    rules={[{required: true, message: '1-15', pattern: /^([01]?[0-5])$/}]}
                    name='itemE'
                >
                    <Input placeholder='1-15' disabled={dataJson.save == 1} suffix=''  />
                </FormItem>

                <FormItem
                    label={'(10)'}
                    rules={[{required: true, message: '1-10', pattern: /^([1-9]|10)$/}]}
                    name='itemF'
                    
                >
                    <Input placeholder='1-10' disabled={dataJson.save == 1}  suffix='' />
                </FormItem>
                <FormItem
                    noStyle
                    shouldUpdate={true}
                >
                    {({ getFieldsValue }) => {
                        const values = Object.values(omit(getFieldsValue(), 'comment')).filter(item => item) as any
                        const sum = values.reduce((pre, cur) => {
                            pre = Number(pre) + Number(cur)
                            return pre
                        }, 0)
                        return(
                            <Form.Item
                                label={''}
                            >
                                <div style={{color: 'red', fontSize: '20px'}}>
                                    {sum}
                                </div>    
                            </Form.Item>
                        ) 
                    }}
                </FormItem>
                <FormItem
                    label={''}
                    rules={[{required: false, message: ''}]}
                    name='comment'
                    
                >
                    <Input.TextArea  rows={6} disabled={dataJson.save == 1} />
                </FormItem>
                <div className='tc' style={{width: '300px', margin: '0 auto'}}>
                    <Row gutter={16}>
                        <Col span={12}>
                            <Button
                                size='middle'
                                onClick={() => onFinish(0)}
                                block
                                disabled={dataJson.save == 1}
                            >
                                
                            </Button>
                        </Col>
                        <Col span={12}>
                            <Button
                                type="primary"
                                htmlType="submit"
                                size='middle'
                                onClick={() => onFinish(1)}
                                block
                                disabled={dataJson.save == 1}
                            >
                                
                            </Button>
                        </Col>
                    </Row>
                </div>
            </Form>
            </Card>
            
        </div>
    )
}

export default Index