import React from 'react'
import { Modal, Button, Checkbox, Space} from 'antd'
import './style.less'

const Index = (props) => {

    const {visible, onClose} = props

    const [check, setCheck] = React.useState(false)
    const [buttonCheck, setButtonCheck] =  React.useState(false)
    const [count, setCount] = React.useState(0)

    React.useEffect(() => {
        setTimeout(() => {
          if (count > 0) {
            setCount((c: number) => c - 1);
          }
        }, 1000);
    }, [count])

    const onFinsh = ()=> {
        console.log(1111)
        onClose(true)
    }
 

    return (
        <Modal
            visible={visible}
            footer={null}
            bodyStyle={{
                maxWidth: '800px',
            }}
        >
            <div className='add_promise'>
                <h3></h3>
                <p>2022</p>

                <p className='p1'>2022</p>
                <p className='p1'>1</p>
                <p  className='p1'>2</p>
                <p  className='p1'>3 </p>
                <p  className='p1'>4</p>
                <p  className='p2'></p>
                <p  className='p3'>*</p>
                <div><Checkbox checked={check} onChange={() => {
                    setCheck(!check); 
                    if (!check) setCount(5)
                }} /> </div>
                <Space style={{width: '100%', justifyContent: 'center'}} wrap >
                    <Button type={buttonCheck ? 'primary': 'default'} disabled={count>0 || !check} onClick={() => setButtonCheck(!buttonCheck)}>{count == 0 ? '': `${count}s` }</Button>
                    <Button type='primary' disabled={!check || !buttonCheck} onClick={onFinsh}></Button>
                </Space>
            </div>
        </Modal >
    )
}

export default Index