import React, { useState } from 'react'
import { Form, Input, Button, Row, Col, Radio, message, Cascader } from 'antd'
import { Link, useHistory  } from '@friday/router'
import { dispatchAsync } from '@friday/async'
import { useApiSelector, useUserInfo } from 'src/hooks'
import options from 'src/common/city.json'
import loginLoign from 'src/pages/home/images/login-m.png'
import { omit } from 'lodash'
import './index.less'


const FormItem = Form.Item

const Index = () => {

    const [form] = Form.useForm()
    const apis = useApiSelector()

    const [time, setTime] = useState(60)
    const [isShowCode, setIsShowCode] = useState<boolean>(false)
    const history = useHistory()

    const sendCode = async () => {
        const fileds = await form.validateFields(['phone'])
        if (isShowCode) {
            return 
        }
        const { phone } = fileds
        const { error, data } = await dispatchAsync(apis.user.sendCode({
            phone,
            type: 0
        }))
        if (error) return 
        setIsShowCode(true)
        const active = setInterval(() => {
            setTime((preSecond) => {
              if (preSecond <= 1) {
                setIsShowCode(false)
                clearInterval(active)
                // 
                return 60
              }
              return preSecond - 1
            })
        }, 1000)
        message.success('')
    }

    const onFinish = async () => {
        const values = await form.validateFields()
        const respone = await dispatchAsync(apis.user.register({
            ...omit(values, 'pw2'),
        })) as any
        if (respone.error) return 
        message.success('')
        history.push('/user')
    } 

    return (
        <div className='m-login'>
            <div className='m-login-b'>
                <div className='m-login-l'>
                </div>
            <div className='m-login-content'>
                <div className='m-login-bg'>
                <div className='m-login-main'>
                    <h3 className='m-title'>
                        <img src={loginLoign} />
                    </h3>
                    <div className='m-login-form'>
                    <Form form={form} 
                        labelCol={{ span: 5 }}
                        onFinish={onFinish}
                        layout="vertical"
                    >
                        <FormItem 
                            name='phone' 
                            label='' 
                            rules={[{required: true, message: '', pattern: /^1[3456789]\d{9}$/ }]} 
                        >
                            <Input placeholder="" size='middle' />  
                        </FormItem>
                        <FormItem 
                            name='code' 
                            label=''
                            rules={[{required: true, message: ''}]}
                        >
                            <Input  
                                placeholder="" 
                                size='middle' 
                                maxLength={6}
                                suffix={<a onClick={() => sendCode()}>
                                  {isShowCode ? `${time}` : ''}
                                </a>}
                            />
                        </FormItem>
                        <FormItem 
                            name='password' 
                            label='' 
                            rules={[{required: true, message: '6', pattern: /^[0-9A-Za-z]{6,}$/}]}
                        >
                            <Input  type="password" placeholder="6" size='middle' />
                        </FormItem>
                        <FormItem 
                            name='pw2' 
                            label='' 
                            rules={[
                                ({getFieldValue})=>({
                                    validator(rule,value){
                                        if(!value || getFieldValue('password') === value){
                                            return Promise.resolve()
                                        }
                                        return Promise.reject("")
                                    }
                                })
                            ]}
                        >
                            <Input type="password" placeholder="" size='middle' />
                        </FormItem>
                        <Form.Item  wrapperCol={{span: 24}}>
                            <Button
                                className="login-form-button"
                                type="primary"
                                htmlType="submit"
                                size='middle'
                                block
                            >
                                
                            </Button>
                        </Form.Item>
                        <div className='tc  mg-b-20' style={{fontSize: '12px'}}>
                            <Link to='/user/login' className='grey'>
                                
                            </Link>
                        </div>
                    </Form>
                </div>
                </div>
                </div>
            </div>
            </div>
        </div>
    )
}

export default Index